<template>
  <div>
    <section>
      <div class="form-box">
        <div class="form-value">
          <form @submit="loginauthentication" method="post">
            <h2>Login</h2>
            <div class="inputbox">
              <ion-icon name="mail-outline"></ion-icon>
              <input type="email" required v-model="email"/>
              <label for="">Email</label>
            </div>
            <div class="inputbox">
              <ion-icon name="lock-closed-outline"></ion-icon>
              <input type="password" required v-model="pass" />
              <label for="">Password</label>
            </div>
      
            <button type="submit">Log in</button>
          
          </form>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import axios from 'axios';

export default {
    data(){
        return{
            email:"",
            pass:''
        }
    },
    methods:{
      

    async loginauthentication() {
      try {
        const response = await axios.post('http://127.0.0.1:3000/login', {
          email: this.email,
          pass: this.pass
        });
        console.log(response.data);
        alert(`Welcome!!! Explore some car.`)
        this.$router.push("/about")
       
        // Store authentication token in local storage and redirect to dashboard
      } catch (error) {
        console.error(error);
        alert("Failed to login")
        // Display error message to user or redirect to error page},
    }
    }
}}
</script>

