import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'

import HomePage from './components/HomePage.vue'
import AboutPage from './components/AboutPage.vue'
import ContactPage from './components/ContactPage.vue'
import NavBar from './components/NavBar.vue'
import FooterPage from './components/FooterPage.vue'
import LoginUser from './components/LoginUser.vue'

import ServicePage from './components/ServicePage.vue'
import MalePage from './components/MalePage.vue'
import DelhiPage from './components/DelhiPage.vue'
import SignPageuser from './components/SignPageuser.vue'
import PunePage from './components/PunePage.vue'
import PortfolioPage from './components/PortfolioPage.vue'
import PortP1 from './components/PortP1.vue'
import PortP2 from './components/PortP2.vue'
import PortP3 from './components/PortP3.vue'
import PortD1 from './components/PortD1.vue'
import PortD2 from './components/PortD2.vue'
import PortD3 from './components/PortD3.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/', component: HomePage
  },
  {
    path: '/about', component: AboutPage
  },
  {
    path: '/navbar', component: NavBar
  },
  {
    path: '/footer', component: FooterPage
  },
  {
    path: '/logins', component: LoginUser
  },
  {
    path: '/service', component: ServicePage
  },
  {
    path: '/contact', component: ContactPage
  },
  {
    path: '/male', component: MalePage
  },
  {
    path: '/delhi', component: DelhiPage
  },
  {
    path: '/sign', component: SignPageuser
  },
  {
    path: '/pune', component: PunePage
  },
  {
    path: '/port', component: PortfolioPage, name: "PortfolioPage"
  },
  {
  path: '/port/user/:userId',
  name: 'PortfolioPage',
  component: PortfolioPage,
},
{
  path: '/port1', component: PortP1, name: "PortP1"
},
{
  path: '/port2', component: PortP2, name: "PortP2"
},
{
  path: '/port3', component: PortP3, name: "PortP3"
},
{
  path: '/portd1', component: PortD1, name: "PortD1"
},
{
  path: '/portd2', component: PortD2, name: "PortD2"
},
{
  path: '/portd3', component: PortD3, name: "PortD3"
},

  
]

const router = new VueRouter({
  mode: 'history',
  routes
})
Vue.config.productionTip = false

new Vue({
  router: router,
  render: h => h(App),
}).$mount('#app')
